import MathsTask from './maths/index';
import EngToRusTask from './engToRus/index';
import WordsTask from './words/index';
import {sample, map} from 'lodash-es';
//***************** taskManager ******************************* */

export function generateTask(type){
    let task;
    let taskList = {
        "maths": new MathsTask(),
        "engToRus": new EngToRusTask(),
        "words": new WordsTask(),
    }
    if(type){
        task = taskList[type];
    } else{
        task = sample(taskList);
    }
    
    return task;
}



